(function(){
    const medicamentosinput = document.querySelector('#medicamentos');


    if(medicamentosinput){
        let medicamentos = [];
        let medicamentosFiltrados = [];

        obtenerMedicamentos();

        async function obtenerMedicamentos() {
            
                const url = `/api/medicamentos`;
                const respuesta= await fetch(url);
                const resultado = await respuesta.json();

                formatearMedicamentos(resultado)

        }
        function formatearMedicamentos(arrayMedicamentos = []) {
            medicamentos = arrayMedicamentos.map( medicamento => {

                return {
                    nombre: `${medicamento.nombre.trim()} ${medicamento.especificaciones.trim()}`,
                    id: medicamento.id


                }
            })
            console.log(medicamentos)
        }
    }
}) ();